import slugify from 'slugify';
import { shape, string } from 'prop-types';
import React, { useCallback } from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'gatsby';

import { useQueryString } from 'hooks';
import { Page, Marker, Pagination } from 'components/widgets';

import { selectFromQuerystring } from './selectors';
import withStyle from './style';

const Home = ({ className, location }) => {
  const querystring = useQueryString(location);
  const { results, pages, found } = useSelector(
    selectFromQuerystring(querystring)
  );
  const renderResult = useCallback(({ id, title }) => {
    const url = `/job/${id}/${slugify(title)}`.toLowerCase();

    return (
      <dl key={id}>
        <dt>
          <Link to={url}>{title}</Link>
        </dt>
        <dd>
          <Marker id={id} />
        </dd>
      </dl>
    );
  }, []);

  return (
    <Page title="Home" className={className}>
      <h1>Latest jobs ({found} found)</h1>
      {results.map(renderResult)}
      <Pagination total={pages} {...querystring} />
    </Page>
  );
};

Home.propTypes = {
  className: string.isRequired,
  location: shape({ search: string.isRequired }).isRequired,
};

Home.defaultProps = {};

export default withStyle(Home);
